<template>
    <div>
      <!-- 头部 -->
        <homeheader :categories=categories @change=change></homeheader>
     <!-- 轮播图 -->
     <cube-slide style="height:150px" :data="items"/>
    
    </div>
    
</template>
<script>
import homeheader from "@/components/HomeHeader.vue";
import {category,slide} from '../api/home'
import * as types from '../store/actions-type.js'

import {createNamespacedHelpers} from 'vuex';
let {mapState,mapActions,mapMutations} = createNamespacedHelpers('home')
export default {
   async created(){
//    let res=  await category()  
//    console.log(res)
   this[types.GET_CATEGORY]()
   let res=await slide();
   
     this.items=res
     console.log(res)
   
   
    },
  data() {
    return {
      items: [
      ]
    };
  
  },
  computed:{
    ...mapState(['categories'])
  },
  components:{
      homeheader
  },
  methods:{

      ...mapActions([types.GET_CATEGORY]),
      ...mapMutations([types.SELECT_INDEX]),
      change(index){
        console.log(index)
       this[types.SELECT_INDEX](index)
      }
  }
};
</script>
