        icon: 'cubeic-person'
      }]
    }
  },
  methods:{

    changeHandler (label) {
     this.$router.push(label)
     
    }
  },
  watch:{
    $router:{
      handler(to,from){
        this.selectedLabelDefault=to.path;
      }
    }
  }
  
}
</script>
